  transform-origin: 0 100%;
  transform: skewY(-1.5deg);
  content: '';
  display: block;
  height: 50%;
  left: 0;
  position: absolute;
  right: 0;
  z-index: -1;
  outline: 1px solid transparent; }

.skewed__reverse--both:before {
  top: 0; }

.skewed__reverse--both:after,
.skewed__reverse--both:before {
  background: inherit;
  content: '';
  display: block;
  height: 50%;
  left: 0;
  position: absolute;
  right: 0;
  z-index: -1;
  outline: 1px solid transparent; }

.skewed__reverse--both:after {
  bottom: 0;
  transform: skewY(-1.5deg);
